// Custom file upload form

// Loosely based on code/design by Codrops
// https://tympanus.net/codrops/2015/09/15/styling-customizing-file-inputs-smart-way/

// Sample HTML:

//  <form method="post" enctype="multipart/form-data" class="gaepd-form-fileupload">
//      <input type="file" name="files" id="files" multiple="multiple" class="filesUploadInput" accept=".jpg,.jpeg" />
//      <label for="files" class="filesUploadLabel">
//      <kbd class="usa-button usa-button-outline usa-button-active">&nbsp;</kbd><span class="usa-button usa-button-active">Choose files...</span>
//      </label>
//      <span class="filesUploadInfo"><em>(Only image file types are supported.)</em></span>
//      <button type="submit" class="filesUploadButton">Upload selected files</button>
//  </form>

.gaepd-form-fileupload {
  .js & {
    input {
      width: 0.1px;
      height: 0.1px;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      z-index: -1;
    }

    button {
      display: none;
    }
  }

  label {
    display: block;
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    overflow: hidden;
    margin: 0 1em 0 0;
  }

  .no-js & label {
    display: none;
  }

  input:focus + label,
  input.has-focus + label,
  input + label:hover {
    box-shadow: 0 0 3px #3e94cf, 0 0 7px #3e94cf;
  }

  kbd,
  span {
    display: block;
    width: 100%;
    padding: 1rem 2rem;
    margin: 0;
  }

  kbd {
    font-weight: normal;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 0.3rem 0.3rem 0 0;
  }

  span {
    border-radius: 0 0 0.3rem 0.3rem;
  }

  button {
    display: inline-block;
    margin-top: 0;
    margin-bottom: 0;
    vertical-align: top;
  }

  @media (min-width: $small-screen) {
    & {
      label,
      kbd,
      span {
        display: inline-block;
        vertical-align: top;
        width: auto;
      }

      kbd {
        width: 200px;
        border-radius: 0.3rem 0 0 0.3rem;
      }

      span {
        border-radius: 0 0.3rem 0.3rem 0;
      }
    }
  }
}
