// Additional input size
input {
  &.gaepd-input-medium-large {
    /* stylelint-disable-line selector-no-qualifying-type */
    @include media($medium-screen) {
      max-width: 18rem;
    }
  }
}

// Disabled Select element
select:disabled {
  color: $color-gray-dark;
  border-color: $color-gray-light;
  background-color: $color-gray-lightest;
  cursor: not-allowed;
}

// Checkbox & label
[type="checkbox"] {
  &:hover {
    cursor: pointer;
  }

  & + label:hover,
  &:hover + label {
    color: #205493;
  }

  & + label + span {
    display: block;
    margin-left: 1.8rem;
    padding-left: 0.6em;
    font-style: italic;
  }
}

// Datepicker input
/* 
    Show the datepicker popup calendar above the nav menu (z-index: 9000),
    but below the Lightbox2 overlay (z-index: 9999).

    !important is required because the datepicker applies a calculated
    z-index to the popup calendar.
*/
.ui-datepicker {
  z-index: 9500 !important;
}
