// Modified from...
// $base-font-size:      rem(17px) !default;
// $small-font-size:     rem(14px) !default;
// $lead-font-size:      rem(20px) !default;
// $title-font-size:     rem(52px) !default;
// $h1-font-size:        rem(40px) !default;
// $h2-font-size:        rem(30px) !default;
// $h3-font-size:        rem(20px) !default;
// $h4-font-size:        rem(17px) !default;
// $h5-font-size:        rem(15px) !default;
// $h6-font-size:        rem(13px) !default;

$base-font-size-print: rem(12px);
$small-font-size-print: rem(10px);
$lead-font-size-print: rem(14px);
$title-font-size-print: rem(37px);
$h1-font-size-print: rem(28px);
$h2-font-size-print: rem(21px);
$h3-font-size-print: rem(14px);
$h4-font-size-print: rem(12px);
$h5-font-size-print: rem(11px);
$h6-font-size-print: rem(10px);

$input-padding-vertical-print: 0.5rem;
$input-height-exact-print: (
   ($base-font-size-print * $input-line-height) +
    (
      $input-padding-vertical-print * 2
    ) + (rem($input-border-width * 2))
);
// truncated to 1 decimal place
// (i.e., 1.21rem -> 1.2rem)
$input-height-print: floor($input-height-exact-print * 10) / 10;

@media not print {
  .gaepd-printonly {
    display: none;
  }
}

@media print {
  // Remove unnecessary elements
  .gaepd-noprint,
  button,
  .gaepd-buttonrow,
  .usa-menu-btn,
  .usa-nav,
  .usa-footer-return-to-top,
  #gagov_external_blocks-topper,
  .gaepd-footer {
    display: none;
  }

  // Page breaks
  p {
    orphans: 2;
    widows: 2;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  thead {
    orphans: 2;
    widows: 2;
    page-break-after: avoid;
    break-after: avoid;
  }

  // Fix layout
  .usa-layout-docs {
    display: block;
  }

  // Font sizes
  body,
  input,
  textarea,
  select,
  button,
  .usa-button {
    font-size: $base-font-size-print;
  }
  title {
    font-size: $title-font-size-print;
  }
  h1 {
    font-size: $h1-font-size-print;
  }
  h2 {
    font-size: $h2-font-size-print;
  }
  h3 {
    font-size: $h3-font-size-print;
  }
  h4 {
    font-size: $h4-font-size-print;
  }
  h5 {
    font-size: $h5-font-size-print;
  }
  h6 {
    font-size: $h6-font-size-print;
  }
  .usa-font-lead {
    font-size: $lead-font-size-print;
  }
  .usa-text-small {
    font-size: $h6-font-size-print;
  }
  .usa-hero-callout {
    .usa-button {
      font-size: $small-font-size-print;
    }
  }
  .usa-label {
    font-size: $h5-font-size-print;
  }
  .usa-label-big {
    font-size: $base-font-size-print;
  }
  legend {
    font-size: $h3-font-size-print;
  }

  // Tables
  table {
    th,
    td {
      padding: 1rem;
    }
    .usa-button {
      &.usa-button-outline,
      &.usa-button-outline-inverse {
        box-shadow: none;
      }
    }
  }

  .gaepd-table-compact {
    margin: 1em 0;
    table {
      th,
      td {
        padding: 0.5rem;
      }
    }
  }

  // HR
  hr {
    border-top: 1px solid $color-base;
    height: 0;
    background-image: none;
  }

  // Inputs
  input,
  textarea,
  select {
    height: $input-height-print;
    padding-top: 0;
    padding-bottom: 0;
  }
  
  // File attachments
  a.gaepd-file_item {
    padding: 0;
    box-shadow: none;
  }

  .gaepd-file_item {
    &-img {
      width: 90px;
      box-shadow: none;
      text-align: center;
    }

    svg {
      height: 1.5em;
    }
  }
}