// Grid container
.usa-grid,
.usa-grid-full {
  width: 100%;
}

// Forms
legend {
  font-size: $h3-font-size;
  padding-top: 2rem;
}

label,
input[type="radio"] + label {
  margin-top: 1rem;
}

// Form/section
form.usa-section {
  @include padding(0 null $site-margins);
}

// Search
.usa-search {
  [type="search"],
  .usa-search-input {
    border-bottom-left-radius: 0.3rem;
    border-top-left-radius: 0.3rem;
  }
}

// Validation alerts
.usa-alert {
  margin-bottom: 1.5em;

  .validation-summary-valid {
    display: none;
  }

  &.usa-alert-error .validation-summary-errors {
    color: $color-secondary-darkest;
  }
}

// Sticky alerts
@supports (position: sticky) {
  .usa-alert {
    position: -webkit-sticky;
    position: sticky;
    top: 1rem;
    z-index: 100;
    box-shadow: 0 2px 4px -1px $color-gray-warm-light;
  }
}
