// File attachments and download links

// Sample HTML:
// <a class="gaepd-file-link">
//     <img />
//     <svg class="gaepd-svgicon"></svg>
//     Title
// </a>

.gaepd-file-link {
  color: #112e51;

  img {
    vertical-align: middle;
    margin-right: 0.5rem;
  }

  svg {
    margin-right: 0.5rem;
  }
}

a.gaepd-file-link {
  text-decoration: none;
  display: block;
  padding: 1rem 0.5rem;
  border-radius: 2px;
  color: #112e51;

  &:hover,
  &:focus {
    box-shadow: inset 0 0 0 2px #205493;
  }
}

// Grid-style tiles

// Sample HTML:
// <div class="usa-grid-full gaepd-file_list">
//   <a class="gaepd-file_item usa-button-outline">
//     <div class="gaepd-file_item-img">
//       <img />
//     </div>
//     <div class="gaepd-file_item-body">
//       Title
//       <br>
//       <em><small>623.65 KB</small></em>
//     </div>
//   </a>
// </div>

.gaepd-file_item {
  svg {
    height: 90px;
    margin-left: 1rem;
  }

  &-img {
    flex-shrink: 0;
    margin-right: 1rem;
    height: 90px;
  }

  &-body {
    width: 100%;
    -ms-word-break: break-all;
    word-break: break-word;
  }
}

a.gaepd-file_item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1rem 0.5rem;
  margin-bottom: 0.5em;
  margin-right: 1%;
  text-decoration: none;
  border-radius: 2px;
  color: #205493;
  box-shadow: inset 0 0 0 2px #dce4ef;

  &:hover,
  &:focus {
    box-shadow: inset 0 0 0 2px #205493;
  }

  @include media($medium-screen) {
    float: left;
    width: 49%;
  }

  @include media($nav-width) {
    width: 32%;
  }
}
