.gaepd-label-dev {
  @extend .usa-label-big;
  margin-right: 0.5rem;
  padding: 0 0.7rem;
  background-color: #ff1496;

  @include media($nav-width) {
    padding: 0.1rem 0.7rem;
    position: absolute;
    left: 0;
    opacity: 0.9;
    transform: scale(1.7);
    transform-origin: left;
  }
}

.gaepd-label-uat {
  @extend .gaepd-label-dev;
  background-color: #ff7800;
}
