hr {
    border: 0; 
    height: 1px; 
    background-image: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.75), transparent);
}

.usa-nav-submenu hr { 
    @include media($nav-width) {
        background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.75), transparent);
    }
}